import React, { Component } from 'react';

import { changePassword } from '../../services/auth';
import { ComponentProps } from '../ComponentProps.d';

import CompPasswordChangeForm from '../../views/comp-password-change-form';

const INITIAL_STATE = {
  oldPassword: '',
  newPassword: '',
  newPasswordConfirmation: '',
  error: '',
};

interface PasswordChangeFormProps extends ComponentProps {}
type PasswordChangeStates = typeof INITIAL_STATE;

class PasswordChangeForm extends Component<PasswordChangeFormProps, PasswordChangeStates> {
  state = INITIAL_STATE;

  onUpdateField = (e: React.ChangeEvent): void => {
    //@ts-ignore
    this.setState({[e.target.name]: e.target.value});
  }

  onSubmit = async (event: React.FormEvent): Promise<void> => {
    event.preventDefault();

    const res = await changePassword(this.state.oldPassword, this.state.newPassword, this.state.newPasswordConfirmation);
    if (res.kind === 'ok') {
      this.setState({...INITIAL_STATE, error: 'success'});
    }
    else {
      this.setState({ error: res.kind });
    }
  };

  render(): React.ReactElement {
    const { oldPassword, newPassword, newPasswordConfirmation, error } = this.state;
    const isSubmitButtonDisabled = !_isValidPassword(this.state);

    return (
      <CompPasswordChangeForm
        onSubmit={this.onSubmit}
        oldPassword={oldPassword}
        newPassword={newPassword}
        newPasswordConfirmation={newPasswordConfirmation}
        onChange={this.onUpdateField}
        isSubmitButtonDisabled={isSubmitButtonDisabled}
        errorMessage={error}
        {...this.props}
      />
    );
  }
}

export {
  PasswordChangeForm
}

const _isValidPassword = (form: any): boolean => (
  form.oldPassword !== '' &&
  form.newPassword !== '' &&
  form.newPasswordConfirmation !== '' &&            // not empty value
  form.oldPassword !== form.newPassword &&          // diff with old one
  form.newPassword === form.newPasswordConfirmation // same with confirmation one
);
