
import React from 'react';
import { withTranslation } from "react-i18next"

import { Layout } from "../components/Layout";
import SEO from "../components/SEO";
import { PasswordChangeForm } from "../components/PasswordChange";
import { LoginManagement } from "../components/LoginManagement";

const I18N_ID = 'Others';

/**
 * User Profile
 * 
 * @param {function} t i18n function
 */
const PageUserProfile = ({
  t, user
}) => (
  <Layout>
    {/* page title */}
    <SEO title={t("user.home")} />

    <h3>{t("user.account")}: {user.email}</h3>

    <br/>
    <h5>{t("user.change-password")}: </h5>
    <PasswordChangeForm t={t}/>

    <br/>
    <h5>{t("user.signin-method")}: </h5>
    <LoginManagement authUser={user} />
  
  </Layout>
);

export default withTranslation(I18N_ID)(PageUserProfile);
