import React from 'react';

/**
 * Password change form view
 * 
 * @param {TFunction} t i18n function
 * @param {(e: FormEvent<Element>) => Promise<void>} onSubmit
 * @param {string} oldPassword
 * @param {string} newPassword
 * @param {string} newPasswordConfirmation
 * @param {(e: ChangeEvent<Element>) => void} onChange
 * @param {boolean} isSubmitButtonDisabled
 * @param {string} errorMessage
 */
const CompPasswordChangeForm = ({
  t,
  onSubmit,
  oldPassword,
  newPassword,
  newPasswordConfirmation,
  onChange,
  isSubmitButtonDisabled,
  errorMessage
}) => (
  <form onSubmit={onSubmit}>
    <input
      name="oldPassword"
      value={oldPassword}
      onChange={onChange}
      type="password"
      placeholder="Current Password"
    /> <br/>
    <input
      name="newPassword"
      value={newPassword}
      onChange={onChange}
      type="password"
      placeholder="New Password"
    /> <br/>
    <input
      name="newPasswordConfirmation"
      value={newPasswordConfirmation}
      onChange={onChange}
      type="password"
      placeholder="New Password Confirmation"
    /> <br/>
    <button disabled={isSubmitButtonDisabled} type="submit">
      {t('auth.form-change-password-button')}
    </button> <br/>

    {errorMessage && <p>{errorMessage}</p>}
  </form>
);

export default CompPasswordChangeForm;
